//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$familyMontserrat: 'Montserrat';

@font-face {
	@include fontdefault(normal, 900, $familyMontserrat);
	@include fontpath('Montserrat Black', 'montserrat-black', montserrat-black); }
@font-face {
	@include fontdefault(italic, 900, $familyMontserrat);
	@include fontpath('Montserrat Black Italic', 'montserrat-blackitalic', montserrat-blackitalic); }

